@import url('https://fonts.googleapis.com/css2?family=Electrolize&family=Pixelify+Sans:wght@400..700&display=swap');

body {
    font-family: "Electrolize", sans-serif !important;
    letter-spacing: 1px;
}

a:active {
    text-decoration: none !important;
    color: #1b1b1b !important;
}

a {
  text-decoration: none !important;
  color: #1b1b1b !important;
}

.fa-caret-right {
    display: none;
    margin: 0 !important;
    padding: 0px 6px !important;
}
  p .list{
    height: 0;
    margin: 0;
  }
  
  a {
    color: #1b1b1b;
    text-decoration: none;
  }

.text-xxs {
    font-size: calc(0.9em);
    
}

.pixel-font {
    font-family: "Pixelify Sans", sans-serif !important;
}

.text-pixel {
    font-family: "Pixelify Sans", sans-serif !important;
    text-shadow: 2px 2px #000000, 3px 3px #ffffff;
}

.pixel-plain-shadow {
    font-family: "Pixelify Sans", sans-serif !important;
    text-shadow: 2px 2px #000000, 3px 3px #ffffff;
}


.nav-link:not(:has(.active)):hover {
    animation: glitch 1s steps(100) infinite;
}

.nav-link:hover .fa-caret-right {
    display: inline-block !important;
    color: #1b1b1b !important;
}

.active a {
    font-weight: bold !important;
    font-family: "Pixelify Sans", sans-serif !important;
    font-weight: 900 !important;
    font-size: 115% !important;
}

.linkedin:hover path {
    fill: #0a66c2 !important;
}

.github:hover path {
    fill: #6e5494 !important;
}

.behance:hover path {
    fill: #1769ff !important;
}

#NavMenu {
    min-height: 100vh;
}

.transition-container {
    animation-name: ease-in-animation;
    animation-duration: 2s;
}


.matrix-gif{
    width: 7.5%;
    height: 7.5%;
}

.books-gif{
    width: 8%;
    height: 8%;
}

.gif{
